@import './variables';
.contact-section {
    background: $white;
    a{
        color: $primary-color;
    }
  }
  
  .contact-form {
    background: #f0f8ff;
    border-radius: 10px;
    padding: 40px;
  }
  
  .contact-info-wrap {
    padding-top: 40px;
  }
  
  .contact-image-wrap {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    margin-top: 20px;
    margin-bottom: 30px;
    padding-bottom: 30px;
    width: 100%;
  }
  .avatar-image {
  border-radius: 100px;
  width: 65px;
  height: 65px;
  object-fit: cover;
}