@import './variables';
@import './mixin';
.slider_bg_1{
    background-image: url(../../img/home.jpg);
}
.slider_area{
    .single_slider{
        height: 742px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        @media #{$mobile_device} {
            height: 500px;
        }
        @media #{$tablet_device} {
            height: 700px;
        }
        .slider_text{
            >span{
                text-transform: capitalize;
                font-size: 26px;
                color: $white;
                font-weight: bold;
                font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                display: block;
            }
            h3{
                color: $white;
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                font-size: 84px;
                margin-top: 6px;
                margin-bottom: 9px;
                line-height: 1.14;
                font-weight: bolder;
             @media #{$mobile_device} {
               font-size: 33px;
               margin-bottom: 20px;
            }
             @media #{$tablet_device} {
                font-size: 55px;
            }
            }
            p{
                font-size: 16px;
                font-weight: bold;
                color: rgb(17, 16, 16);
                margin-bottom: 40px;
                margin-top: 10px;
             @media #{$mid_device} {
                font-size: 16px;
             }
             @media #{$mobile_device} {
                font-size: 16px;
             }
            }
        }
    } 
}
.boxed-btn3 {
	color: #222222;
	display: inline-block;
	padding: 13px 44px;
	font-size: 16px;
    font-weight: 600;
    border: 0;
    border-radius:5px;
    text-align: center;
    text-transform: capitalize;
    transition:.5s;
    background: #fff;
    font-family: sans-serif;
    cursor: pointer;
    &:hover{
        background: #3CC78F;
        color: #fff;
    }
    &:focus{
        outline: none;
    }
    &.large-width{
        width: 220px;
    }
}
.section-padding {
    padding-top: 50px;
    padding-bottom: 50px;
    .featured-block {
    text-align: center;
    transition: all 0.5s ease;
    min-height: 256px;
    padding: 15px;
    &:hover{
        background: $white;
    border-radius: 20px;
    box-shadow: 0 1rem 3rem rgba(0,0,0,.175);
    }
    &:hover.featured-block-image {
        transform: scale(0.75);
      }
      &:hover.featured-block-text {
        margin-top: 0;
      }
}
  
.featured-block-image {
    display: block;
    margin: auto;
    transition: all 0.5s;
}
  
.featured-block-text {
    color: $primary-color;
    font-size: 24px;
    margin-top: 20px;
    transition: all 0.5s;
}
  h2{
    color: #5A6F80;
    font-size: 54px;
    font-weight: bold;
  }
}
.reason_area{
    h3{
        font-size: 54px;
        font-weight: bold;
        color: $text;
        padding: 5rem;
    }
    .single_reason{
        position: relative;
        @media #{$mobile_device} {
            margin-bottom: 30px;
        }
        @media #{$tablet_device} {
            margin-bottom: 40px;
        }
        .thum{
            margin-right: 84px;
            padding-bottom: 84px;
            .thum_1{
                border-radius:9px;
                overflow: hidden;
                img{
                    width: 100%;
                    border-radius:9px;
                    transform:scale(1);
                    transition:.3s;
                }
            }

        }
        .help_content{
            background: #fff;
            position: absolute;
            bottom: 0;
            right: 0;
            border-radius:7px;
            box-shadow:(0 3px 6px rgba(0, 0, 0, .16));
            padding: 47px 40px 33px 40px;
            left: 46px;
            h4{
                font-size: 20px;
                font-weight: 400;
            }
            p{
                margin-top: 21px;
                margin-bottom: 30px;
            }
            .read_more{
                font-size: 16px;
                color: #BCBDBF;
                text-transform: capitalize;
                position: relative;
                display: inline-block;
                padding-left: 68px;
                &::before{
                    position: absolute;
                    left: 0;
                    top: 50%;
                    width: 36px;
                    height: 2px;
                    background: #707070;
                    content: '';
                    transform: (translateY(-50%));
                    transition:(.3s);
                }
                &:hover{
                    color:#3CC78F ;
                    &::before{
                        background: #3CC78F;
                    }
                }
            }
        }
        &:hover{
            .thum{
                img{
                    transform:(scale(1.1));
                }
            }
        }
    }
}
.section-padding1 {
  padding-top: 100px;
  padding-bottom: 100px;
}
.section-bg {
  background-color:  #f0f8ff;
  height: auto;
}
.custom-text-box {
  background: $white;
  border-radius: 20px;
  margin-bottom: 24px;
  padding: 40px;
}
.custom-block-wrap {
  background: $white;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  transition: all 0.5s;
}

.custom-block-wrap:hover {
  box-shadow: 0 1rem 3rem rgba(0,0,0,.175);
}
.custom-text-box-image {
  border-radius: 20px;
  width: 100%;
  object-fit: cover;
}

.custom-text-box-icon {
  background:  #f0f8ff;
  border-radius: 100px;
  color: #5a6f80;
  font-size: 22px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  width: 25px;
  height: 25px;
  line-height: 30px;
}
.custom-list {
  margin-bottom: 0;
  padding-left: 0;
}
.custom-text-box,
  .volunteer-form {
    padding: 30px;
  }
.custom-list-item {
  list-style: none;
  margin-top: 10px;
  margin-bottom: 10px;
}
.counter-thumb {
  margin: 20px;
  margin-bottom: 0;
}

.counter-number,
.counter-text {
  color: $text;
  display: block;
}

.counter-number,
.counter-number-text {
  color: $primary-color;
  font-size: 54px;
  font-weight: bold;
  line-height: normal;
}
.custom-block-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.custom-block-body {
  padding: 30px;
  h5{
    color: $text;
  }
}
.custom-block .custom-btn {
  border-radius: 0;
  display: block;
}
.progress {
  background: #e9eaeb;
  height: 5px;
}

.progress-bar {
  background: #5a6f80;
}
.custom-btn {
  background: #5a6f80;
  border: 2px solid transparent;
  border-radius: 100px;
  color: $white;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  padding: 15px 25px;
  &:hover{
        background: $primary-color;
        color: $white;
  }
}
.cta-section {
  position: relative;
  overflow: hidden;
  h2{
    color: #5A6F80;
  }
}

.cta-section::before {
  content: "";
  background-color: $primary-color;
  border-radius: 50%;
  position: absolute;
  top: -100px;
  left: -30px;
  margin: auto;
  width: 200px;
  height: 200px;
}

.cta-section::after {
  content: "";
  border: 20px solid #597081;
  border-radius: 50%;
  position: absolute;
  bottom: -100px;
  right: 0;
  left: 0;
  margin: auto;
  width: 150px;
  height: 150px;
}
.section_title{
  h3{
      font-size: 50px;
      font-weight: 400;
      color: #191d34;
      z-index: 1;
      font-family: sans-serif;
      line-height: 1.3;
      span{
          position: relative;
          display: inline-block;
          z-index: 2;
          &::before{
              position: absolute;
              left: 0;
              bottom: 0;
              background: #3CC78F;
              height: 10px;
              width: 100%;
              content: '';
              opacity: .5;
              bottom: 7px;
              z-index: -1;
          }
      }
      @media #{$mobile_device} {
          font-size: 25px;
          line-height: 36px;
      }
      @media #{$tablet_device} {
          font-size: 36px;
          line-height: 42px;
      }
      br{
          @media #{$mobile_device} {
              display: none;
          }
      }
  }
  p{
      font-size: 16px;
      color: #597081;
      line-height: 30px;
      margin-bottom: 0;
      br{
          @media #{$mobile_device} {
              display: none;
          }
      }

  }
}
.mb-100{
  margin-bottom: 100px;
  @media #{$mobile_device} {
      margin-bottom: 40px;
  }
}
.mb-55{
  margin-bottom: 55px;
  @media #{$mobile_device} {
      margin-bottom: 40px;
  }
}
.counter-thumb{
  .number{

    font-weight: 700;
    font-size: 28px;
    color: $primary-color;
  }
}