@import './variables';

.logo{
    color: $primary-color;
    font-weight: 900;
    &:hover{
        color: $text;
    }
}
.navbar-expand-lg .navbar-nav .nav-link{
    color: $text;
    font-size: large;
    font-weight: bold;
    &:hover{
        color: $white;
        background-color: $text;
    }
}
.bg-body-tertiary{
    background: #3CC78F !important;
}
.book_btn{
    margin-left: 30px;
    a{
     background: #fff;
     padding: 12px 26px;
     font-size: 16px;
     font-weight: bold;
     color: #222;
     border-radius:(4px);
     @media #{$mid_device} {
         padding: 12px 20px;
     }
     &:hover{
         background: $text;
         color: #3CC78F;
         transition: .5s;
     }
    }
}
.dropdown-item.active, .dropdown-item:active {
    background-color: #3CC78F;
}